// player.js
	function printPlayerListAll(data) {
		document.getElementById('playerList-Home').innerHTML = '';
		$.each(data, function( index, value ) {
			var li = document.createElement('li');
			li.innerText = value.name;
	    li.dataset['player'] = value.name;
	    li.dataset['id'] = value.id;
			document.getElementById('playerList-Home').appendChild(li);
		});
	  $( '#playerList-Home li' ).wrapInner( '<strong></strong>' );
	  $( '#playerList-Home li strong' ).after( '<span class="btn primary" data-action="rate" title="rate"></span><span class="btn primary" data-action="herocard" title="rate"></span><span class="btn secondary" data-action="erase" title="erase"></span>' );

			document.getElementById('playerList-Visitor').innerHTML = '';
		$.each(data, function( index, value ) {
			var li = document.createElement('li');
			li.innerText = value.name;
			li.dataset['player'] = value.name;
			li.dataset['id'] = value.id;
			document.getElementById('playerList-Visitor').appendChild(li);
		});
		$( '#playerList-Visitor li' ).wrapInner( '<strong></strong>' );
		$( '#playerList-Visitor li strong' ).after( '<span class="btn primary" data-action="rate" title="rate"></span><span class="btn primary" data-action="herocard" title="rate"></span><span class="btn secondary" data-action="erase" title="erase"></span>' );


	}




	function loadPlayerListsReal() {
		$.ajax({
		  url: 'app/api.php/player',
		  method: 'GET'
		}).done(function(data) {
			var playerList = JSON.parse(data);
	    printPlayerListAll(playerList);
		});
	}



