// tables.js

function printTableResult(data) {
  //document.getElementById('tableResult').innerHTML = '';
  $.each(data, function( index, value ) {


    var tableResultTr = document.createElement('tr');

    var tableResultRank = document.createElement("td");
    tableResultRank.setAttribute("class", "table-rank" );
    tableResultRank.innerText = '1';


    var tableResultName = document.createElement("td");
    tableResultName.setAttribute("class", "table-name" );
    tableResultName.innerText = value.name;

    var tableResultNation = document.createElement("td");
    tableResultNation.setAttribute("class", "table-nation" );
    tableResultNation.innerText = value.nationality;


    var tableResultPoints = document.createElement("td");
    tableResultPoints.setAttribute("class", "table-points" );
    tableResultPoints.innerText = value.points;


    var tableResultGoals = document.createElement("td");
    tableResultGoals.setAttribute("class", "table-goals" );
    tableResultGoals.innerText = value.goals  + ":" +  value.goalsAgainst;

    var tableResultGoalsDif = document.createElement("td");
    tableResultGoalsDif.setAttribute("class", "table-goals-dif" );
    tableResultGoalsDif.innerText = value.goalsDiff;

    var tableResultGoalsWin = document.createElement("td");
    tableResultGoalsWin.setAttribute("class", "table-strake" );
    tableResultGoalsWin.innerText = value.wins;

    var tableResultGoalsDraw = document.createElement("td");
    tableResultGoalsDraw.setAttribute("class", "table-strake" );
    tableResultGoalsDraw.innerText = value.draws;

    var tableResultGoalsLos = document.createElement("td");
    tableResultGoalsLos.setAttribute("class", "table-strake" );
    tableResultGoalsLos.innerText = value.losses;


    tableResultTr.appendChild(tableResultRank);
    tableResultTr.appendChild(tableResultName);
    tableResultTr.appendChild(tableResultNation);
    tableResultTr.appendChild(tableResultPoints);
    tableResultTr.appendChild(tableResultGoals);
    tableResultTr.appendChild(tableResultGoalsDif);
    tableResultTr.appendChild(tableResultGoalsWin);
    tableResultTr.appendChild(tableResultGoalsDraw);
    tableResultTr.appendChild(tableResultGoalsLos);

    tableResult.appendChild(tableResultTr)

  });


  var Head = $('<tr>' +
    '<th title="Position"></th>' +
    '<th title="Player" class="table-name"></th>' +
    '<th title="Player" class="table-nation"></th>' +
    '<th title="Points" class="table-points">Pts</th>' +
    '<th title="Goals" class="table-goals">G</th>' +
    '<th title="Goal Difference" class="table-goals-dif">GD</th>' +
    '<th title="Wins" class="table-strake">W</th>' +
    '<th title="Draws" class="table-strake">D</th>' +
    '<th title="Losses" class="table-strake">L</th>' +
  '</tr>'
  );

    function wrapTableResult() {
      $( '#tableResult tr' ).wrapAll('<tbody/>');
      $( '#tableResult tbody' ).wrapAll('<table id="tableResult-Table"/>');
      $('#tableResult-Table tr').first().before(Head);

    }

    wrapTableResult();

    function setHeadlineTableResult() {
      $( '#tableResult-Table' ).before( '<h3 data-frontend="panel-headline-all" class="arrow-direction">Results Only Table</h3>' );
    }

    setHeadlineTableResult();

    function setNationClass() {

        $('td.table-nation').each(function () {
            var nationclass = $(this).text();
            $(this).addClass(nationclass);
            $(this).text('');
            $(this).append('<span class="flag"></span>');
          });

    }

    setNationClass();

}


function loadTableResult() {
  $.ajax({
    url: 'app/api.php/resultsRanking',
    method: 'GET'
  }).done(function(data) {
    var results = JSON.parse(data);


    results.sort(
       function(a,b){
          if (a.points==b.points){
             return parseFloat(b.goalsDiff) - parseFloat(a.goalsDiff);
          } else {
             return parseFloat(b.points) - parseFloat(a.points);
          }
       });



    printTableResult(results);
    function setIndexPos(){
      $('#tableResult-Table tr').each(function(i) {
        $(this).find('td').first().each(function() {
          $(this).text(i);
        });
      });
    }
    setIndexPos();
  });
}

loadTableResult();



function printTableRating(data) {

  $.each(data, function( index, value ) {
    var tableRatingTr = document.createElement('tr');

    var tableRatingRank = document.createElement("td");
    tableRatingRank.setAttribute("class", "table-rank" );
    tableRatingRank.innerText = '1';

    var tableRatingName = document.createElement("td");
    tableRatingName.setAttribute("class", "table-name" );
    tableRatingName.innerText = value.name;

    var tableRatingTech = document.createElement("td");
    tableRatingTech.setAttribute("class", "table-skill" );
    tableRatingTech.innerText = value.technique;

    var tableRatingTact = document.createElement("td");
    tableRatingTact.setAttribute("class", "table-skill" );
    tableRatingTact.innerText = value.tactic;

    var tableRatingTeam = document.createElement("td");
    tableRatingTeam.setAttribute("class", "table-skill" );
    tableRatingTeam.innerText = value.teamwork;


    tableRatingTr.appendChild(tableRatingRank);
    tableRatingTr.appendChild(tableRatingName);
    tableRatingTr.appendChild(tableRatingTech);
    tableRatingTr.appendChild(tableRatingTact);
    tableRatingTr.appendChild(tableRatingTeam);

    tableRating.appendChild(tableRatingTr)
  });

  var Head2 = $('<tr>' +
    '<th title="Position"></th>' +
    '<th title="Player" class="table-name"></th>' +
  '</tr>'
  );

  function wrapTableRating() {
    $( '#tableRating tr' ).wrapAll('<tbody/>');
    $( '#tableRating tbody' ).wrapAll('<table id="tableRating-Table" style="display: none;"/>');
    $('#tableRating-Table tr').first().before(Head2);

  }

  wrapTableRating();

  function setHeadlineTableRating() {
    $( '#tableRating-Table' ).before( '<h3 data-frontend="panel-headline-all">Rating Only Table</h3>' );
  }

  setHeadlineTableRating();
}


function loadTableRating() {
  $.ajax({
    url: 'app/api.php/ratingsAVG',
    method: 'GET'
  }).done(function(data) {
    var rating = JSON.parse(data);

    printTableRating(rating);
    function setIndexPos(){
      $('#tableRating-Table tr').each(function(i) {
        $(this).find('td').first().each(function() {
          $(this).text(i);
        });
      });
    }
    setIndexPos();
  });
}


loadTableRating();



