// player.js




function printCardList(data) {
	$.each(data, function( index, value ) {
		var input = document.createElement('input');
		var label = document.createElement('label');
		var img = document.createElement('img');
		//label.innerText = value.name;
		img.src="cardimages/"+ value.url;
		input.setAttribute("id", "heorcard-" + value.id );
		input.setAttribute("type", "radio");
		input.setAttribute("name", "heorcard");
		input.dataset['card'] = value.id;
		input.dataset['name'] = value.name;
		document.getElementById('cards-scale').appendChild(input);
		document.getElementById('cards-scale').appendChild(label);
		label.appendChild(img);
	});

}

function loadCardListsReal() {
	$.ajax({
		url: 'app/api.php/card',
		method: 'GET'
	})
	.done(function(data) {
		var cardList = JSON.parse(data);
		printCardList(cardList);
	});
}

$( '[data-matchside]' ).on( "click", '[data-player] [data-action="herocard"]', function() {

	var nextLi = $( this ).parent().next( 'li' );
	var nextscalelength = $( this ).parent().next( 'li.cards' ).length;
	var datNewNew = $( this ).parent().data( 'player' );
	var this2 = $(this);


	$(this).hide();

	if (nextscalelength == 0) {
		$.ajax({
			url: "snippets/cards.html",
			success: function(result){
			$( nextLi ).after(result);
		}

	}).done(function(data) {
		setTimeout(loadCardListsReal, 10);


	});

	}

	setTimeout(function(){
		 $( nextLi ).next('li').show();
	}, 1000);


	function changeIds() {

		$( '#cards-scale-' + datNewNew)
		$(this2).closest('li').next('li').next('li').find('#cards-scale').each(function(){
			if(this.id){
				this.id = this.id+'-'+datNewNew;
			}
		});

		$(this2).closest('li').next('li').next('li').find( '.cards-scale input' ).each(function(){
			if(this.id){
				this.id = this.id+'-'+datNewNew;
			}
			if(this.name){
				this.name = this.name+'-'+datNewNew;
			}
		});

		$(this2).closest('li').next('li').next('li').find( '.cards-scale label' ).each(function(){
			$(this).attr('for', $(this).prev(":input").attr("id"));
		});

		}

setTimeout(changeIds, 1000);
});
