// result.js

function printResultsAll(data) {
  document.getElementById('resultBlock').innerHTML = '';
  $.each(data, function( index, value ) {

    //var ul = document.createElement("ul");
    //ul.setAttribute("class", "result-list" );

    var li = document.createElement('li');
    li.setAttribute("class", "resultline" );

    var gamehost = document.createElement("div");
    gamehost.setAttribute("class", "game" );
    gamehost.innerText = value.hostTeam;

    var gamevs = document.createElement("div");
    gamevs.innerText = 'vs';

    var gameguest = document.createElement("div");
    gameguest.setAttribute("class", "game" );
    gameguest.innerText = value.guestTeam;

    var gameresult = document.createElement("div");
    gameresult.setAttribute("class", "gameresult" );
    gameresult.innerText = value.host  + ":" +  value.guest;


    var dateData = value.date;
    var stripHere = 10;
    var dateDataNew = dateData.substring(0, stripHere);

    li.dataset['id'] = dateDataNew;


    li.appendChild(gamehost);
    li.appendChild(gamevs);
    li.appendChild(gameguest);
    li.appendChild(gameresult);
    //ul.appendChild(li)
    document.getElementById('resultBlock').appendChild(li);
  });






  function wrapResults() {
  //$( ".resultline" ).wrapAll( "<ul class='result-list' />");

  var $ul = $('.resultline');
      days = {};

  $ul.each(function(){
     days[$(this).data('id')] = '';
  });

  for (day in days) {
    $ul.filter('[data-id='+ day +']').wrapAll('<ul class="result-list" />');
  }

  $( '#resultBlock .result-list' ).before( '<h3 data-frontend="panel-headline-all" class="result-day-headline">Resultslist</h3>' );




  }
  wrapResults()


  function showBlockHeadline() {

    $( '#resultBlock .result-list' ).first().prev( '[data-frontend="panel-headline-all"]' ).addClass( 'arrow-direction' );

    $( '#resultBlock [data-frontend="panel-headline-all"]' ).each(function(){
      var headlineText = $(this).next( '.result-list' ).find('li').last().data( 'id' );
      $(this).text(headlineText);
    });
   }
   showBlockHeadline();



}


function loadResults() {
  $.ajax({
    url: 'app/api.php/resultsList',
    method: 'GET'
  }).done(function(data) {
    var results = JSON.parse(data);

    results.sort(function(a,b){
      return new Date(b.date) - new Date(a.date);
    });

    printResultsAll(results);
  });
}

loadResults();
